@charset "UTF-8";
.main-index {
  position: relative;
  height: 100%;
  width: 100%; }

.main-nav {
  position: fixed;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
      -ms-flex-pack: justify;
          justify-content: space-between;
  bottom: 0;
  width: 100%;
  padding: 2% 5%;
  box-sizing: border-box;
  background-color: #fff; }

.activeName {
  color: #12a9eb; }

.activeName p {
  color: #12a9eb; }

.main-nav::before {
  content: "";
  position: absolute;
  height: 1px;
  background: rgba(117, 117, 117, 0.2);
  width: 100%;
  left: 0;
  top: 0; }

.main-nav li {
  text-align: center;
  font-size: 1rem;
  color: #757575; }

/* 资产界面 */
.assets-all-head {
  width: 100%;
  height: -webkit-calc(220rem/14);
  height: calc(220rem/14);
  background: url(img/icon_bg.51290533.png) no-repeat;
  background-size: 100% 100%; }

.assets-head-qrIcon {
  color: #fff;
  text-align: right;
  padding: 0 2%;
  box-sizing: border-box;
  padding-top: 2%; }

.assets-head-qrIcon span {
  font-size: -webkit-calc(25rem/14);
  font-size: calc(25rem/14); }

/* 行情界面 */
.quotation-cotain-all {
  width: 100%;
  height: 100%;
  background-color: #f4f4f4; }

.quotation-cotain-all ul {
  padding-top: -webkit-calc(10rem/14);
  padding-top: calc(10rem/14);
  width: 100%;
  overflow: scroll;
  box-sizing: border-box;
  height: 100%; }

.quotation-list {
  height: -webkit-calc(80rem/14);
  height: calc(80rem/14);
  width: 100%;
  background-color: #FFFFFF;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 3% 5%;
  box-sizing: border-box;
  text-align: center;
  font-size: 1rem; }

.confirm-tag {
  min-width: -webkit-calc(152rem/2/14);
  min-width: calc(152rem/2/14);
  height: -webkit-calc(52rem/2/14);
  height: calc(52rem/2/14);
  background-color: rgba(33, 33, 33, 0.5);
  border-radius: -webkit-calc(4rem/14);
  border-radius: calc(4rem/14);
  text-align: center;
  color: #FFFFFF;
  line-height: -webkit-calc(52rem/2/14);
  line-height: calc(52rem/2/14); }

.confirm-tag a {
  color: #FFFFFF; }

.quotation-ta {
  margin-top: 4%; }

/* 我的界面 */
.mine-contain {
  height: 100%;
  overflow: scroll;
  width: 100%;
  background-color: #f4f4f4; }

.mine-head-content {
  height: -webkit-calc(200rem/14);
  height: calc(200rem/14);
  width: 100%;
  background-color: #12a9ed;
  position: relative;
  box-shadow: 0 0.125rem 0.3125rem #7DCDF3; }

.mine-head-setting {
  width: 100%;
  padding: 1% 2% 0;
  text-align: right;
  box-sizing: border-box; }

.mine-head-setting .icon-shezhi {
  color: #FFFFFF;
  font-size: -webkit-calc(30rem/14);
  font-size: calc(30rem/14); }

.mine-head-avater {
  /* margin-top: calc(8rem/14); */
  margin: -webkit-calc(8rem/14) auto 0;
  margin: calc(8rem/14) auto 0;
  width: 3.75rem;
  height: 3.75rem; }

.mine-nickname {
  color: #FFFFFF;
  text-align: center;
  font-size: 0.75rem;
  margin-top: 0.5rem; }

.mine-coin {
  color: #FFFFFF;
  text-align: center;
  font-size: 0.75rem;
  margin-top: 0.75rem; }

.mine-sec-order {
  padding: 3% 0;
  background-color: #FFFFFF; }

.sec-order-head {
  font-size: -webkit-calc(16rem/14);
  font-size: calc(16rem/14);
  font-family: "microsoft yahei";
  padding: 0 3%; }

.sec-order-inline {
  display: block;
  height: 1px;
  width: 100%;
  background-color: rgba(33, 33, 33, 0.3);
  margin-top: 3%; }

